import React, { Component } from 'react';
import { Grid, Cell} from 'react-mdl';

class Experience extends Component{

    render() {
        return(
            <Grid>
                <Cell col={10}>
                    <h1 style={{marginTop: '0px'}}><b>{this.props.jobName}</b></h1>
                    <Cell offset={0} col = {12}>
                        <i><h3 style = {{fontSize : '18px'}}>{this.props.tech}</h3></i>
                    </Cell>
                     <ul style = {{marginLeft : '0px'}}>
                        {React.Children.toArray(
                            this.props.test.map((item, i)=> <li style = {{fontSize : '16px'}}>{item}</li>)
                        )}
                     </ul>
                </Cell>
                <Cell col={2}>
                    <p>{this.props.startYear} - {this.props.endYear}</p>
                </Cell>
                
            </Grid>
        )
    }
}

export default Experience;