import React, { Component } from 'react';
import { Grid, Cell} from 'react-mdl';

class Education extends Component{
    render() {
        return(
            <Grid>
                <Cell col={10}>
                    <a style = {{textDecoration : 'none'}}href = "https://www.42.us.org"><h1><b>{this.props.schoolName}</b></h1></a>
                    <h3 style = {{fontSize : '16px'}}><i>Software Engineering</i></h3>
                    <ul style = {{marginLeft : '0px'}}>
                        {
                            <li style = {{fontSize : '16px'}}>{this.props.schoolDescription}</li>
                        }
                     </ul>
                </Cell>
                <Cell col={2}>
                    <p>{this.props.startYear} - {this.props.endYear}</p>
                    <p style = {{fontSize : '12px', marginBottom : 'none'}}>{this.props.city}, {this.props.state}</p>
                </Cell>
            </Grid>
        )
    }
}

export default Education;